}

ol li .SuperRadio_check__3Oht3{
    display: block;
    position: absolute;
    border: 5px solid #AAAAAA;
    border-radius: 100%;
    height: 25px;
    width: 25px;
    top: 30px;
    left: 20px;
    z-index: 5;
    transition: border .25s linear;
    -webkit-transition: border .25s linear;
}

ol li:hover .SuperRadio_check__3Oht3 {
    border: 5px solid #FFFFFF;
}

ol li .SuperRadio_check__3Oht3::before {
    display: block;
    position: absolute;
    content: '';
    border-radius: 100%;
    height: 15px;
    width: 15px;
    top: 5px;
    left: 5px;
    margin: auto;
    transition: background 0.25s linear;
    -webkit-transition: background 0.25s linear;
}

input[type=radio]:checked ~ .SuperRadio_check__3Oht3 {
    border: 5px solid #0DFF92;
}

input[type=radio]:checked ~ .SuperRadio_check__3Oht3::before{
    background: #0DFF92;
}

